/* src/pages/Home.css */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.logo {
  width: 60px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.home {
  margin-top: 4rem;
  text-align: center;
}

.categorias {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1rem;
}

.categoria {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.categoria img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid #ccc;
}

/* Grid de productos adaptable */
.grid-productos {
  display: grid;
  gap: 1.5rem;
  margin-top: 2rem;
  justify-content: center;
}

/* MÃ³vil: 1 columna bien centrada */
@media (max-width: 600px) {
  .grid-productos {
    grid-template-columns: 100%;
    justify-content: center;
    padding: 0 1rem;
  }

  .producto-card {
    margin-left: auto;
    margin-right: auto;
    max-width: 90%;
  }
}


/* Tablet: 2 columnas */
@media (min-width: 601px) and (max-width: 900px) {
  .grid-productos {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* PC: 3 columnas */
@media (min-width: 901px) {
  .grid-productos {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Escritorio grande: 4 columnas */
@media (min-width: 1200px) {
  .grid-productos {
    grid-template-columns: repeat(4, 1fr);
  }
}
